<template>
   <nav>
       <div class="nav-wrapper green">
           <router-link to="/" class="brand-logo mt-10"><span class="left"> &nbsp; &nbsp; HR-Portal &nbsp; &nbsp; </span> </router-link>
           <ul class="right">
               <li v-if="isLoggedIn"> <span class="email black-text">{{currentUser}}</span>
               <li v-if="isLoggedIn"><router-link to="/">Dashboard</router-link></li>
               <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
               <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
               <li v-if="isLoggedIn"><button v-on:click="logout" class="btn grey">Logout</button></li>
            </ul>
       </div>
   </nav>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data () {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = 1; 
            this.currentUser = firebase.auth().currentUser.email;
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(() => {
                this.$router.go({ path: this.$router.path })
            })
        }
    }
}
</script>

<style scoped>
    .email {padding-right: 10px;}

</style>
